import React, { MouseEvent } from "react";
// import Icon from "../../assets/images/icons/arrow-right.svg";
import { ButtonStyles as Styled } from "./button.styles";

// const MyComponent = () => <ArrowDownIcon />;
export const Button = ({ text, Icon, onClick, style, theme = "primary" }) => {
  const click = (e: MouseEvent<HTMLElement>) => {
    e.stopPropagation();

    onClick && onClick();
  };

  return (
    <Styled.ButtonWrapper style={style}>
      <Styled.Wrapper theme={theme} onClick={click}>
        <Styled.ButtonText>{text}</Styled.ButtonText>
        {Icon && <Icon />}
      </Styled.Wrapper>
    </Styled.ButtonWrapper>
  );
};
